                        </v-card-actions>
                    </v-card>
                </v-dialog>-->

            </v-menu>

            <v-menu
                medium
                icon
                v-model="menu"
                :close-on-content-click="false"
                :nudge-width="270"
                :nudge-top="15"
                offset-y
                >                     
                <template v-slot:activator="{ on }">                    
                    <div class="my-2">                         
                        <v-btn 
                            v-on="on"
                            icon
                            slot="activator"
                            v-show="$session.get('logged')"
                        >
                            <v-icon>person</v-icon>
                        </v-btn>                            
                    </div>                         
                </template>               

                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card color="lighten-3" >
                            <v-layout style="padding:10px">              
                            <v-flex xs5 >
                                <v-hover>
