            >
            <v-layout pa-2 column class="primary lightbox white--text">
                <v-spacer></v-spacer>

                <v-list-item>
                    <v-list-item-avatar>              
                        <v-img :src="require(`../../assets/img/${$store.getters.getAvatar}`)" />
                    </v-list-item-avatar>
                </v-list-item>

                <v-flex shrink>
                    <div class="subheading" style="margin-left: 12px">{{ $session.get('nombre') }}</div>
                    <div class="body-1" style="margin-left: 12px">@{{ $session.get('usuario') }}</div>
                    <div>                        
                        <v-tooltip bottom style="margin-right: 7px" v-show="$session.get('logged')">
                            <template v-slot:activator="{ on }">
                                <v-btn medium v-on="on" dark icon @click="cerrarsesion">
                                    <v-icon>exit_to_app</v-icon>
