        <v-dialog v-model="dialog_notificaciones" max-width="1200px">
            <v-card color="lighten-3" >
                <v-btn     
                    color ="primary"
                    class="mt-2 ml-5"
                    text
                    @click="Notificaciones"                                                                
                >
                    <v-icon>refresh</v-icon>
                </v-btn>

                <v-layout style="padding:10px">                                
                    <v-data-table 
                        :headers="headers" 
                        :items="notificaciones" 
