                    </v-btn>
                    <v-btn                       
                        text
                        color="primary"
                        @click="dialog_notificaciones=false"                        
                        class="ml-5"
                    >
                        Cancelar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialog_del" max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline red--text">Ocultar Notificaciones</span>
                </v-card-title>        
                <v-card-text>					
                    ¿Esta seguro de ocultar las Notificaciones seleccionadas?					
                </v-card-text>        
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" dark text @click="dialog_del=false">Cancelar</v-btn>
                    <v-btn color="red" text @click="OcultarNotificaciones" >Ocultar Notificaciones</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>  

<script>
import notificacionesService from "../../servicios/NotificacionesService"

export default {
    name:'Toolbar',
    components: {
        'change-password' : () => import('@/components/frames/CChangePassword.vue'),  
        'cmenu' : () => import('@/components/frames/CMenu.vue'), 
        'configura-alertas' : () => import('@/components/frames/CConfiguraAlertas.vue'),     
    },    
    data(){
        return {
            modulo:'SISTEMA',
            cambiar_pass: false,
            configura_alertas:false,
            drawer: false,
            menu: false,
            menu2: false,
            appName: 'SINCHI - CTR',
            nombrecorto:'',
            selected: [],
            headers: [
  					{
			          text: 'Fecha',
			          align: 'left',
			          sortable: false,
			          value: 'fecha',
                    },
                    { text: 'Origen', value: 'opcion',sortable: false,align: 'left'},
                    { text: 'No. Interno', value: 'numerointerno',sortable: false,align: 'left'},
                    { text: 'Mensaje', value: 'mensaje',sortable: false,align: 'left'}                   
                  ],
            notificaciones: [], 
            search:'',
            totalnotificaciones:'',
            shownotification:false,
            dialog_del:false,
            dialog_notificaciones:false            
        }    
    },
    methods: {
        showMessage (snack, time, color, text) {

            this.$root.$emit('showSnack', {snack: snack, time: time, color: color, text: text})

        },	
        cerrarsesion() {

            this.drawer = false
            this.agregaBitacora()
            this.$session.destroy()            
            this.$router.push('/')
        },
        editPass() {
            this.cambiar_pass = true
        },
        editAlertas() {
            this.configura_alertas = true           
        },
        abrircerrarMenu(){

            this.drawer = !this.drawer

        },
        agregaBitacora( ){
              
            var accion = 'SALIDA'
            var detalle = 'SALIDA DEL SISTEMA'      

            var data = {modulo:this.modulo,accion:accion,detalle:detalle}
            
            this.$root.$emit('agregaBitacora', data)

        },

        setearNotificacion(){

            var total = this.notificaciones.length
            this.$store.commit('setNotificacion', total)

            if(total> 0){
                this.totalnotificaciones = total
                this.shownotification = true
            } else {
                this.totalnotificaciones = ''    
                this.shownotification = false
            }

        },
        
        Notificaciones(){

            var _this = this
            this.loading = true            

            notificacionesService.getDataXId(this.$session.get('usuarioid'))
                .then(function(res) {

                    if (res.request) {                        

                        if(res.data){

                            _this.notificaciones = res.data.data                        							

                        }  else {
                        
                            _this.notificaciones = []                           
                        
                        }

                    } else {                        	

                        _this.notificaciones = []                        	
                    }    
                    _this.setearNotificacion()
                    _this.loading = false                
                    
            }).catch(function(err) {

                _this.notificaciones = []   
                _this.setearNotificacion()
                _this.loading = false                
                
            })

        },

        OcultarNotificaciones(){            

            var _this = this
            this.loading = true            
            
            notificacionesService.ocultarNotificaciones(_this.selected)
                .then(function(res) {

                    if(res.statusText){                    

                        _this.dialog_del = false	              	  		
                        _this.Notificaciones()                        

                        var data = {modulo:'NOTIFICACIONES DEL USUARIO',accion:'OCULTAR',detalle:'SE OCULTARON NOTIFICACIONES DEL SISTEMA'}
                
                        _this.$root.$emit('agregaBitacora', data)

                    }else{	                                         	

                        _this.showMessage(true,3000,'error',res.message) 	                  	
                    }
                                        
                    _this.loading = false	                 
                    
            }).catch(function(err) {

                _this.showMessage(true,3000,'error',err)	    
                _this.loading = false          	
                
            })
        }
    },
    mounted() {

        this.nombrecorto = this.$session.get('nombre').charAt(0).toUpperCase() + this.$session.get('nombre').slice(1).toLowerCase()
        this.nombrecorto = this.nombrecorto.length <= 15 ? this.nombrecorto : this.nombrecorto.slice(0, 15) + '...'
        
        this.Notificaciones()        
    }
}

</script>